import React, { Component } from 'react';

import { Modal, Button,Input, Form } from 'antd';

class DescribeCom extends Component {
    constructor(props){
        super(props);
    }

    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        this.props.form.validateFields((err, values) => {
            console.log(err);
            console.log(values);
            if (!err) {
                that.props.handleSubmitDevice(values);
            }
        })
    }

    /**
     * 
     */
    handleDeviceModalCancel = ()=>{
        this.setState({
            visible:false
        });
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        const { visible,newDeviceData} = this.props.data;


        return (
            <div>
                    {/*  */}
                    <Modal
                        visible={visible}
                        title=""
                        onCancel={this.props.handleDeviceModalCancel}
                        width="600px"
                        className="copyStoreModal"
                        centered={true}
                        footer={[
                            <Button key="back" onClick={this.props.handleDeviceModalCancel}></Button>,
                            <Button key="submit" type="primary" onClick={this.handleSubmit}>
                            
                            </Button>,
                        ]}
                        >
                        <div >
                        <Form onSubmit={this.handleSubmit} ref="describe">
                            <Form.Item>
                            {getFieldDecorator('longDescribe', {
                                rules: [{
                                    required: true, message: '!',
                                },{
                                    pattern: /^[^0-9\s]{1,10}$/, message:'(10)!',
                                }],
                                initialValue: newDeviceData,
                            })(
                                <Input type="text" placeholder="" />
                            )}
                            </Form.Item>
                        </Form>
                        </div>
                    </Modal>


            </div>
        )
    }

}

export default Form.create({ name: 'describe' })(DescribeCom)