import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { getShopDetail,updateShop,getFoodsStyle,getAreaStyle,getSecondLabels } from '../../../config/getData';
import { getStorage } from '../../../config/mUtils'
import BreadcrumbCustom from '../../common/BreadcrumbCustom';
import OssUploadImg from '../../common/ossUpload/OssUploadImg';
import DeviceFrom from './DeviceForm'

import { Card, Row, Col, Icon, Avatar, Modal, Button, Checkbox, message, Table, Divider, Tag, Input, Select, Pagination, Form, Tooltip, Cascader, AutoComplete, DatePicker, TimePicker } from 'antd';
import history from '../../public/history';
import moment from 'moment';

import './StoreManagement.scss';

const format = 'HH:mm';
const { Option } = Select;
const { TextArea } = Input;
const AutoCompleteOption = AutoComplete.Option;
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;

// 
const selectShowArrow = true;

// 
const uploadStoreImgFormatParms = { 
    uploadImgLen: 9, 
    maskCont: { isShow: true, text: "" },
    folder:"BmanagementPlatform/storeImg/store" };
// 
const uploadLongImgFormatParms = { 
    uploadImgLen: 1, 
    maskCont: { isShow: false, text: "" },
    folder:"BmanagementPlatform/storeImg/long" };


class StoreManagement extends Component {
    constructor(props) {
        super(props);

        this.state = {
            foodClassData:[], //
            areaStyleData:[], //
            secondLabelsData:[], //
            deviceModal:{
                visible:false,   //
                loading:false,  //loading
                newDeviceData:"", //
            },
            initData: {
                shopName:"", //
                classId:[], //
                shopImage:[        //
                    // {
                    //     "type":0,    //--  0:   1:
                    //     "url":"https://zhuanyoyo.oss-us-west-1.aliyuncs.com/test1/test/201904221114501702820.png"
                    // },{
                    //     "type":1,    //--  0:   1:
                    //     "url":"https://zhuanyoyo.oss-us-west-1.aliyuncs.com/test1/test/201904221132511989942.jpeg"
                    // }
                ],
                shopAddress:"", //
                shopArea:"", //
                contactWay:"", //
                averageConsume:"", //
                openTime:{ //
                    "shopOpenDateIds": [
                    //   "1","3"
                    ],
                    "shopOpenTimes": [
                    //   {
                    //     "shopOpenTime": "9:20",
                    //     "shopCloseTime": "14:30",
                    //   },
                    //   {
                    //     "shopOpenTime": "17:20",
                    //     "shopCloseTime": "23:30",
                    //   }
                    ]
                },
                indoorDevice: [], //
                customDevice:[], //
                indoorAndCustomDevice:[], //
                shopIntro: "", //
                longDescribe:"", //
                longTitle:"", //
                // longImagePath:"https://zhuanyoyo.oss-us-west-1.aliyuncs.com/test1/test/201904221132511989942.jpeg" //
                longImagePath:"" //
            }
        };

        this.deviceRef = React.createRef();
    }

    componentDidMount() {
        this.initPageData();
    } 

    /**
     * 
     */
    initPageData = () => {
        // ,
        console.log(getStorage("storeMsgStorage").toJson() )
        getShopDetail({ shopId: getStorage("storeMsgStorage").toJson().shopId }).then((res) => {
            console.log(res)
            var resData = res.data;
            if(resData.errorCode === "0000" && resData.data){
                var initData = resData.data;
                initData.openTime = !initData.openTime ? this.state.initData.openTime : initData.openTime;
                initData.indoorDevice = !initData.indoorDevice ? this.state.initData.indoorDevice : initData.indoorDevice;
                initData.customDevice = !initData.customDevice ? this.state.initData.customDevice : initData.customDevice;
                // initData.shopImage = !initData.shopImage ? this.state.initData.shopImage : initData.shopImage;
                // initData.longImagePath = !initData.longImagePath ? this.state.initData.longImagePath : initData.longImagePath;
                //s
                initData.indoorDevice.forEach((item,index)=>{
                    this.state.initData.indoorAndCustomDevice.push(item.shopSetLabelId);
                })
                initData.customDevice.forEach((item,index)=>{
                    this.state.initData.indoorAndCustomDevice.push(item.shopLabelName);
                })
                initData.indoorAndCustomDevice = this.state.initData.indoorAndCustomDevice;

                console.log(initData)
                this.setState({
                    initData:initData
                })
                console.log(this.state.initData)
                this.initFoodClassData(); //
                this.initAreaStyle(); //
                this.initSecondLabels(); //

            }
        })
    }

    /**
     * 
     */
    initFoodClassData = () => {
        getFoodsStyle().then((res)=>{
            var resData = res.data;
            if(resData.errorCode === "0000"){
                this.setState({
                    foodClassData:resData.data
                })
            }
        })
    }

    /**
     * 
     */
    initAreaStyle = () => {
        getAreaStyle().then((res)=>{
            var resData = res.data;
            if(resData.errorCode === "0000"){
                this.setState({
                    areaStyleData:resData.data
                })
            }
        })
    }
    

    /**
     * 
     */
    initSecondLabels = () => {
        getSecondLabels({labelType:0}).then((res)=>{
            var resData = res.data;
            if(resData.errorCode === "0000"){
                this.setState({
                    secondLabelsData:resData.data
                })
            }
        })
    }

    /**
     * 
     */
    onChangeUploadShopImg = (res) => {
        // 
        res.map((item,index)=>{
            if(index === 0){
                item.type = 1;
            }else{
                item.type = 0;
            }
        })
        this.state.initData.shopImage = res;
    }
    
    /**
      * 
      */
    onChangeUploadLongImg = (res) => {
        if(res.length){
            this.state.initData.longImagePath = res[0].url;
        }else{
            this.state.initData.longImagePath = "";
        }
    }

    /**
     * 
     */
    openTimePickerChange = (flag,index,e) => {
        if(!e) return false;
        var shopOpenTimes = this.state.initData.openTime.shopOpenTimes;
        if(flag === "o"){
            shopOpenTimes[index].shopOpenTime = e.format(format);
        }else if(flag === "c"){
            shopOpenTimes[index].shopCloseTime = e.format(format);
        }
        this.setState({
            initData:this.state.initData
        })
    }

    /**
     * 
     */
    addNewTimes= ()=>{
        this.state.initData.openTime.shopOpenTimes.push(                      {
            "shopOpenTime": "",
            "shopCloseTime": "",
          })
        this.setState({
            initData:this.state.initData
        })
    }

    /**
     * 
     */
    openTimePickDelete= (index,e)=>{
        this.state.initData.openTime.shopOpenTimes.splice(Number(index),1);
        this.setState({
            initData:this.state.initData
        })
    }

   /**
    * 
    */
   addIndoorDevice = ()=>{
        this.setState({ deviceModal: {
            visible:true
        }});
        this.deviceRef.current.resetFields();
    }

    /**
     * 
     */
    handleDeviceModalCancel = ()=>{
        this.setState({ deviceModal: {
            visible:false
        }});
    }

    /**
     * 
     */
    changeDeviceVal = (res) => {
        // this.state.initData.indoorDevice = res;
        this.state.initData.indoorAndCustomDevice = res;
        this.setState({
            initData:this.state.initData
        })
    }

    /**
     * 
     */
    handleSubmitDevice = (res) => {
        this.handleDeviceModalCancel();
        message.success("!");
        // this.state.initData.indoorDevice.push(res.longDescribe);
        this.state.initData.indoorAndCustomDevice.push(res.longDescribe);
        this.setState({
            initData:this.state.initData
        })
    }

    /**
     * 
     */
    handleSubmit = (e) => {
        e.preventDefault();
        let that = this;
        console.log(this.state.initData)
        this.props.form.validateFields((err, values) => {
            console.log(err);
            console.log(values);
            //Form
            if (!err) {
                var reqData = values;
                var stateData = this.state.initData;
                reqData.shopId = getStorage("storeMsgStorage").toJson().shopId; //id
                reqData.shopImage = stateData.shopImage; //
                if(!stateData.openTime.shopOpenTimes.length){
                    message.error("!");
                    return false;
                }
                reqData.openTime =  stateData.openTime; //
                reqData.openTime.shopOpenDateIds =  reqData.shopOpenDateIds; //
                if(!stateData.longImagePath){
                    message.error("!");
                    return false;
                }
                reqData.longImagePath = stateData.longImagePath; //

                //
                reqData.customDevice = [];
                reqData.indoorDevice = [];
                values.indoorAndCustomDevice.forEach((item,index)=>{
                    // 
                    if(/^[0-9]*$/.test(item)){
                        this.state.secondLabelsData.forEach((itemLabel,index)=>{
                            if(itemLabel.shopSetLabelId == item){
                                reqData.indoorDevice.push(itemLabel);
                            }
                        })
                    //
                    }else{
                        reqData.customDevice.push({
                            shopLabelName: item,
                            shopLabelOrder: null,
                            shopQuantity: 0,
                            shopSetLabelId: null
                        })
                    }
                })       
                console.log('Received reqData of form: ', reqData);
                //
                updateShop(reqData).then((res)=>{
                    console.log(res)
                    if(res.data.errorCode === "0000"){
                        message.success("!")
                    }else{
                        message.error(res.data.errorMsg);
                    }
                })
                
            }else{
                message.error(",!");
            }
        })
    }
    

    // dom
    /**
     * 
     */
    serviceOptions = () => {
        let options = this.state.initData.indoorDevice.map((item,index)=>{
            return (
                <Option value={item} key={item}>{item}</Option>
            )
        })
        return options;
    }

    /**
     * 
     */
    openTimePickerDom = () => {
        let openTimePickerDom = this.state.initData.openTime.shopOpenTimes.map((item,index)=>{
            return (
                <Row gutter={20} className="shopOpenTimes" key={index}
                >
                    <Col xs={24} sm={24} md={24} lg={12}>
                        <TimePicker 
                            value={item.shopOpenTime ? moment(item.shopOpenTime, format) :undefined}
                            onChange={(e)=>{this.openTimePickerChange("o",index,e)}}
                            format={format} placeholder="" style={{ width: '140px' }} />
                        <span className="andTo"></span>
                        <TimePicker 
                            value={item.shopCloseTime ? moment(item.shopCloseTime, format) : undefined}
                            onChange={(e)=>{this.openTimePickerChange("c",index,e)}}
                            format={format} placeholder="" style={{ width: '140px' }} />
                        <Tag color={"#f50"} className="andTo" onClick={(e)=>{this.openTimePickDelete(index,e)}} ></Tag>
                    </Col>
                </Row>
            )
        })
        
        return openTimePickerDom;
    }

    /**
     * 
     */
    foodClassData = () => {
        // console.log(this.state.foodClassData)
        let foodClassDataOption = this.state.foodClassData.map((item,index)=>{
            return(
                <Option value={item.shopStyleId} key={item.shopStyleId}>{item.shopStyleName}</Option>
           )
        })
        return foodClassDataOption;
    }

    /**
     * 
     */
    areaStyleData = () => {
        // console.log(this.state.areaStyleData)
        let areaStyleDataOption = this.state.areaStyleData.map((item,index)=>{
            return(
                <Option value={item.areaId} key={item.areaId}>{item.areaName}</Option>
           )
        })
        return areaStyleDataOption;
    }

    /**
     * 
     */
    serviceLabelsData = () => {
        // console.log(this.state.secondLabelsData)
        let secondLabelsDataOption = this.state.secondLabelsData.map((item,index)=>{
            return(
                <Option value={item.shopSetLabelId} key={item.shopSetLabelId}>{item.shopLabelName}</Option>
           )
        })
        return secondLabelsDataOption;
    }
    
     
    render() {
        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 24 },
                md: { span: 24 },
                lg: { span: 20 },
            },
        };

        return (
            <div>
                <div className='storeManagementCont'>
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Card
                            title={<span><span style={{ color: '#ADADAD' }}> / </span><span></span></span>}
                            extra={<div>
                                <Button type="primary" style={{ marginLeft: "10px" }} onClick={(e) => { this.handleSubmit(e) }}></Button>
                            </div>}>
                            <Row gutter={60} className="storeManageItemCont">
                                <Col span={24}>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('shopName', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                max:20, message: '20!',
                                            }],
                                            initialValue: this.state.initData.shopName,
                                        })(
                                              <Input type="text" placeholder="" />
                                        )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">20</Col>
                                    </Row>
                                    </Form.Item>
                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col  xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('classId', {
                                            rules: [{
                                                required: true, message: '!',
                                            }],
                                            initialValue:this.state.initData.classId
                                        })(
                                            <Select placeholder="" >
                                                {this.foodClassData()}
                                            </Select>
                                        )}
                                        </Col>
                                    </Row>
                                    </Form.Item>


                                    <Form.Item
                                        label=""
                                        className="hasBottomDotted"
                                    >
                                        <Row gutter={20} className="storeItemCont">
                                            <Col span={24}>
                                                <OssUploadImg 
                                                    ImgDatas={this.state.initData.shopImage} 
                                                    onChangeUploadImg={this.onChangeUploadShopImg} 
                                                    formatParam={uploadStoreImgFormatParms}>
                                                    </OssUploadImg>
                                            </Col>
                                            <Col span={24} className="ossUploadImgTips">
                                                :750x750()jpg,1M,9()
                                            </Col>
                                        </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col  xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('shopArea', {
                                            rules: [{
                                                required: true, message: '!',
                                            }],
                                            initialValue:this.state.initData.shopArea
                                        })(
                                            <Select placeholder="">
                                                {this.areaStyleData()}
                                            </Select>
                                        )}
                                        </Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('shopAddress', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                max:100, message: '100!',
                                            }],
                                            initialValue:this.state.initData.shopAddress
                                        })(
                                            <Input type="text" placeholder="" />
                                        )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">100</Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('contactWay', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                pattern: /^[\d-]{10,12}$/, message: '!', 
                                            }
                                            ],
                                            initialValue:this.state.initData.contactWay
                                        })(
                                                <Input type="text" placeholder="" />
                                            )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips"></Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('averageConsume', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                pattern: /^[\d\.]+[\d]{0,10}$/, message: '!', 
                                            },
                                            ],
                                            initialValue:this.state.initData.averageConsume
                                        })(
                                                    <Input type="text" placeholder="" suffix={
                                                        <span>$</span>
                                                    } />
                                            )}
                                            </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">/,</Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                        className="hasBottomDotted"
                                    >
                                        <Row gutter={20} className="shopOpenDateIds"
                                        >
                                            <Col xs={24} sm={24} md={24} lg={12}>
                                            {getFieldDecorator('shopOpenDateIds', {
                                                rules: [{
                                                    required: true, message: '!',
                                                }],
                                                initialValue:this.state.initData.openTime.shopOpenDateIds
                                            })(
                                                <Select placeholder="" 
                                                        showArrow={selectShowArrow}
                                                        mode="multiple" >
                                                        <Option value="1"></Option>
                                                        <Option value="2"></Option>
                                                        <Option value="3"></Option>
                                                        <Option value="4"></Option>
                                                        <Option value="5"></Option>
                                                        <Option value="6"></Option>
                                                        <Option value="7"></Option>
                                                </Select>
                                            )}
                                            </Col>
                                        </Row>

                                        {this.openTimePickerDom()}

                                        <Row gutter={20} className="shopOpenTimes" 
                                             style={{display:(this.state.initData.openTime.shopOpenTimes.length === 3) ? "none" : "block"}}
                                        >
                                            <Col xs={24} sm={24} md={24} lg={12}>
                                                <Button type="primary" onClick={(e)=>{this.addNewTimes()}}>+</Button>
                                            </Col>
                                        </Row>

                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col  xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('indoorAndCustomDevice', {
                                            initialValue: this.state.initData.indoorAndCustomDevice,
                                        })(
                                            <Select placeholder="" 
                                                    mode="multiple" 
                                                    onChange={(e)=>{this.changeDeviceVal(e)}} 
                                                    showArrow={selectShowArrow}>
                                               {/* {this.serviceOptions()} */}
                                               {this.serviceLabelsData()}
                                            </Select>
                                        )}
                                        </Col>
                                    </Row>
                                    <Row gutter={20} className="addIndoorDeviceBtn">
                                        <Col  span={12}>
                                            <Button type="dashed" onClick={()=> {this.addIndoorDevice()}} style={{color:"#d9d9d9",width:"100%"}}>+ </Button>
                                        </Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                    >
                                        <Row gutter={20} className="storeItemCont">
                                            <Col xs={24} sm={24} md={24} lg={12}>
                                                {getFieldDecorator('shopIntro', {
                                                    rules: [{
                                                        max:100, message: '200!',
                                                    }],
                                                    initialValue: this.state.initData.shopIntro,
                                                })(
                                                    <TextArea rows={4} placeholder="" />
                                                )}
                                            </Col>
                                            <Col xs={24} sm={24} md={24} lg={12} className="sicTips">200</Col>
                                        </Row>
                                    </Form.Item>

                                </Col>
                            </Row>
                        </Card>

                        <Card
                            className='smcCardItem'
                            title={<span></span>}>
                            <Row gutter={60} className="storeManageItemCont">
                                <Col span={24}>
                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('longTitle', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                max:20, message: '20!',
                                            }],
                                            initialValue: this.state.initData.longTitle,
                                        })(
                                                    <Input type="text" placeholder="" />
                                            )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">20</Col>
                                    </Row>
                                    </Form.Item>
                                    <Form.Item
                                        label=""
                                    >
                                    <Row gutter={20} className="storeItemCont">
                                        <Col xs={24} sm={24} md={24} lg={12}>
                                        {getFieldDecorator('longDescribe', {
                                            rules: [{
                                                required: true, message: '!',
                                            },{
                                                max:20, message: '20!',
                                            }],
                                            initialValue: this.state.initData.longDescribe,
                                        })(
                                                    <Input type="text" placeholder="" />
                                        )}
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} className="sicTips">20</Col>
                                    </Row>
                                    </Form.Item>

                                    <Form.Item
                                        label=""
                                        className="longImg"
                                        required
                                    >
                                        <Row gutter={20} className="storeItemCont">
                                            <Col span={24}>
                                                <OssUploadImg ImgDatas={this.state.initData.longImagePath} 
                                                              onChangeUploadImg={this.onChangeUploadLongImg} 
                                                              formatParam={uploadLongImgFormatParms} ></OssUploadImg>
                                            </Col>
                                            <Col span={24} className="ossUploadImgTips">
                                                :750x750()jpg,1M
                                               </Col>
                                        </Row>
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Card>
                    </Form>

                    <DeviceFrom 
                            ref={this.deviceRef}
                            data={this.state.deviceModal} 
                            handleDeviceModalCancel={this.handleDeviceModalCancel} 
                            handleSubmitDevice={this.handleSubmitDevice} 
                            >
                    </DeviceFrom>
                    

                </div>
            </div>
        )
    }


}

export default Form.create({ name: 'storeManage' })(StoreManagement)

