import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import {getShopOverview,shopOnline,shopCopy} from '../../../config/getData';
import {setStorage,getStorage} from '../../../config/mUtils';
import BreadcrumbCustom from '../../common/BreadcrumbCustom';
import { Card, Row, Col, Icon, Avatar, Modal, Button, Checkbox, message, Layout} from 'antd';
import history from '../../public/history';
import './StoreManagement.scss';

import HeaderCustom from '../../common/StoreListHeaderCustom';

const Meta = Card;
const CheckboxGroup = Checkbox.Group;
const {Content, Footer} = Layout;

let copyStoreVal = []; //list

function warning(contentData){
    Modal.warning({
        title:"",
        content:contentData,
        okText:"",
        maskClosable:true,
        width:500,
        centered:true,
        onOk(){
            history.push("/storeItem/storeManagement");
        }
    })
}

export default class StoreManagement extends Component {

    constructor(props){
        super(props);

        this.state = {
            copyModalSureLoading: false,
            copyModalVisible: false,
            onlineModalVisible:false,
            storeListData:[], //
            copyStoreVal:[], //list
            copyHostShop:"", //
            checkedList:[], //
            canOnlineShopId:"", //id
        }
    }

    componentDidMount(){
        //
        this.initPageData();
    }

    /**
     * 
     */
    initPageData = () => {
        // ,
        getShopOverview().then((res)=>{
            // console.log(res)
            var resData = res.data;
            if(resData.errorCode === "0000"){
                var storeListData = resData.data;
                storeListData.map((item,index)=>{
                    item.label = item.shopName;
                    item.value = item.shopId;
                    return item;
                })
                this.setState({
                    storeListData:storeListData,

                })
            }
        })
    }

    /**
     * 
     */
    loadPage = (e) =>{
        var cardBodyStyle = { width: "100%",height:"150px",display:"flex",alignItems:"center"};
        var cardArr = [];
        
        this.state.storeListData.forEach(item => {
            // console.log(item)
            // 
            var cardActionsArr = [];
            cardActionsArr.push(
            <span className="cardActionSpan" onClick={(e)=>{this.selectStore(e,item)}}></span>, 
            <span className="cardActionSpan" onClick={(e)=>{this.copyStore(e,item)}}></span>)
            if(!Number(item.shopStatus)){
                cardActionsArr.push(
                    <span className="cardActionSpan" onClick={(e)=>{this.onlineStore(e,item)}}></span>)
            }
            // 
            cardArr.push(
                <Col span={8} key={item.shopId}>
                <div className="storeBox">
                    <Card
                        bodyStyle={cardBodyStyle}
                        hoverable = {true}
                        actions={cardActionsArr}>
                        <div className="storeCard" style={Number(item.shopStatus) ? {opacity:1}:{opacity:0.5}} onClick={(e)=>{this.selectStore(e,item)}} >
                                <Avatar size={64} icon="user"/>
                                <span className="storeName">{item.shopName}</span>
                        </div>
                        <div style={Number(item.shopStatus) ? {color:"#5ac461"}:{display:"block"}} className="waitOnline">{Number(item.shopStatus) ? "" : "" }</div>
                    </Card>
                    </div>
                </Col>                
            )
        });

        // console.log(cardArr);
        return cardArr;
    }

    /**
     * 
     */
    selectStore = (e,params)=>{
        // console.log(e);
        // console.log(params);
        setStorage('storeMsgStorage',params);//
        history.push("/storeItem/storeManagement");
    }

   /**
    * 
    */
   copyStore = (e,params)=>{
        copyStoreVal = [];
        copyStoreVal = JSON.parse(JSON.stringify(this.state.storeListData)); //list
        //
        _.remove(copyStoreVal,function(item){
            if(item.shopId === params.shopId){
                return true;
            }else{
                return false;
            }
        })
        // console.log(copyStoreVal)
        if(copyStoreVal.length > 0){
            this.setState({ 
                copyModalVisible: true,
                copyHostShop:params
            });
        }
    }

    /**
     * 
     */
    onChangeCheckBox = (e)=>{
        this.setState({ checkedList: e });
    }

    /**
     * 
     */
    handleCopyModalCancel = ()=>{
        this.setState({ 
            copyModalVisible: false,
            checkedList:[]  
         });
    }

    /**
     * 
     */
    handleCopyModalOk = ()=>{
        this.setState({ copyModalSureLoading: true });
        // ,
        var reqData = {
            sourceShopId:this.state.copyHostShop.shopId,
            targetShopIds:this.state.checkedList,
        }
        // console.log(reqData)
        shopCopy(reqData).then((res)=>{
            // console.log(res)
            var resData = res.data;
            if(resData.errorCode === "0000"){
                message.success('!');
                this.initPageData();
                this.setState({ 
                    copyModalVisible: false 
                });
            }else{
                message.error(resData.errorMsg);
            }
            this.setState({ 
                copyModalSureLoading: false, 
            });
        })        
    }

   /**
    * 
    */
   onlineStore = (e,params)=>{
        // console.log(e);
        // console.log(params);
        setStorage('storeMsgStorage',params);//
        this.setState({ onlineModalVisible: true,canOnlineShopId:params.shopId });
    }


    /**
     * 
     */
    handleOnlineModalOk = ()=>{
        // 
        shopOnline({shopId:this.state.canOnlineShopId}).then((res)=>{
            // console.log(res)
            var resData = res.data;
            if(resData.errorCode === "0000"){
                //
                this.initPageData();
            }else{
                //
                var errorMsg = resData.errorMsg || ""; 
                warning(errorMsg);
            } 
            this.setState({ onlineModalVisible: false});
        })
    }

    /**
     * 
     */
    handleOnlineModalCancel = ()=>{
        this.setState({ onlineModalVisible: false });
    }

    render() {
        const { copyModalVisible,onlineModalVisible,copyModalSureLoading } = this.state;
        var storeUser2 = getStorage("mspa_user2").toJson();
        var name = storeUser2.username;
        return (
            <div>
                <HeaderCustom username={name}/>
                <Content style={{margin: '16px 0'}}>
                    <div className='storeManageCont'>
                        <Card
                            title="">
                            <Row gutter={60} className="storeCont">
                                {this.loadPage()}
                            </Row>
                        </Card>

                        {/*  */}
                        <Modal
                        visible={copyModalVisible}
                        title=""
                        onCancel={this.handleCopyModalCancel}
                        width="600px"
                        className="copyStoreModal"
                        centered={true}
                        footer={[
                            <Button key="back" onClick={this.handleCopyModalCancel}></Button>,
                            <Button key="submit" type="primary" loading={copyModalSureLoading} onClick={this.handleCopyModalOk}>
                            
                            </Button>,
                        ]}
                        >
                        <p><span className="colorBlue"></span></p>
                        <div className="checkboxStoreListCont">
                            <CheckboxGroup 
                                    options={copyStoreVal} 
                                    value={this.state.checkedList}
                                    onChange={(e)=>{this.onChangeCheckBox(e)}} />
                        </div>
                        </Modal>

                        {/*  */}
                        <Modal
                        visible={onlineModalVisible}
                        onCancel={this.handleOnlineModalCancel}
                        width="600px"
                        className="onlineStoreModal"
                        centered={true}
                        footer={[
                            <Button key="back" onClick={this.handleOnlineModalCancel}></Button>,
                            <Button key="submit" type="primary" onClick={this.handleOnlineModalOk}>
                            
                            </Button>,
                        ]}
                        >
                        <Icon type="exclamation-circle" theme="filled" style={{fontSize:"64px",color:"#efaf41"}} />
                        <p className="onlineBodyTitle"></p>
                        <p></p>
                        </Modal>

                    </div>
                </Content>
            </div>
        )
    }
}
