import React, { Component } from 'react';
import { getMarketCoupon,deleteMarketCoupon  } from '../../../config/getData';
import { getStorage,removeSessionStorage} from '../../../config/mUtils';
import { Card, Row, Col, Button, message, Table, Tag, Pagination, Modal} from 'antd';
import history from '../../public/history';
import './SellManagement.scss';

export default class sellManageTable extends Component {
    constructor(props){
        super(props);

        this.state = {
            coupon:"", //
            status:"1",//
            getCouponParams:{ //
              pageNum:1,//
              pageSize:10,//
            },
            totalElements:"",//
            totalPages:"",//
            initTableData:[], //
            columns:[{
              align:"center",
              title: '',
              dataIndex: 'couponType',
              key: 'couponType',
              render: (couponType,item) => {
                if(couponType == "0"){
                  return <b></b>
                }else if(couponType == "1"){
                  return <b></b>
                }else if(couponType == "2"){
                  return <b></b>
                }
              }
            }, {
              align:"center",
              title: '',
              dataIndex: 'couponValue',
              key: 'couponValue',
              render: (couponValue,item) => {
                if(item.couponType == "0"){
                  return <span>{couponValue}</span>
                }else if(item.couponType == "1"){
                  return <span>{couponValue}%</span>
                }else if(item.couponType == "2"){
                  return <span>{couponValue}</span>
                }
              }
            }, {
              align:"center",
              title: '',
              dataIndex: 'shareTimes',
              key: 'shareTimes',
              render: (shareTimes,item) => {
                // console.log(shareTimes)
                // console.log(item)
                  return <span>{shareTimes}</span>
              }
            }, {
              align:"center",
              title: '',
              dataIndex: 'shopId',
              render: (shopId,item) => (
                  <span>
                      <Tag color={"#3f90f7"} onClick={(e)=>{this.editCoupon(item)}}></Tag>
                      <Tag color={"#f50"}  onClick={(e)=>{this.deleteTableCoupon(item)}}></Tag>
                  </span>
              ),
          }]
        }
    }

    componentDidMount(){
      this.initPageData();
    }

    /**
     * 
     */
    initPageData = () => {
      var getCouponParams = this.state.getCouponParams;
      getCouponParams.shopId = getStorage("storeMsgStorage").toJson().shopId;
      // console.log(getCouponParams)
      getMarketCoupon(getCouponParams).then((res)=>{
          var resData = res.data;
          // console.log(resData.data)
          if(resData.errorCode === "0000"){
              this.setState({
                  initTableData:resData.data.content,
                  totalElements:resData.data.totalElements,
                  totalPages:resData.data.totalPages,
              })
          }
      })
    }

    /**
     * 
     */
    toggleUseStyle = (e)=>{
      history.push('/storeItem/sellManagement/useCouponStyle',"new")
    }


    /**
     * 
     */
    editCoupon = (item) => {
      item = item ? item : "new";
      removeSessionStorage("couponParam");
      var path = {
        pathname:'/storeItem/sellManagement/ToggleCoupon',
        state:item,
      }
      history.push(path)
    }
    

    /**
     * 
     */
    deleteTableCoupon = (item) => {
      // console.log(item);
      var that = this;
      Modal.confirm({
          title: '?',
          autoFocusButton:"cancel",
          okText:"",
          cancelText:"",
          maskClosable:true,
          width:500,
          centered:true,
          onOk() {
            deleteMarketCoupon({couponId:item.couponId}).then((res)=>{
                  var resData = res.data;
                  console.log(resData);
                  if(resData.errorCode === "0000"){
                      that.initPageData();
                      message.success("!");
                  }else{
                      message.success(resData.errorMsg);
                  }
              })
          },
      });
    }

    // 
    /**
     * --change
     */
    // couponChange(e){
    //   console.log(e.target.value);
    //   this.setState({
    //     coupon:e.target.value
    //   })
    // }

    /**
     * 
     */
    // sureBtn = (e) => {
    //   console.log(this.state.coupon)
    // }

   /**
     * --change
     */
    // statusChange(e){
    //   console.log(e);
    //   this.setState({
    //     status:e
    //   })
    // }

    // pagination
    /**
     * 
     */
    onShowSizeChange = (pageNum, pageSize) => {
      this.state.getCouponParams.pageNum = parseInt(pageNum);
      this.state.getCouponParams.pageSize = parseInt(pageSize);
      this.setState({
          getCouponParams:this.state.getCouponParams
      })
      this.initPageData();
  }

    /**
     *  
     * quickJumper -- 
     * @param pageNum: filters: sorter:
     */
    paginationOnChange = (pageNum,filters, sorter) => {
      // console.log( pageNum)
      var setPageNum = typeof pageNum == "number" ? pageNum : this.state.getCouponParams.pageNum;
      //
      this.state.getCouponParams.pageNum = parseInt(setPageNum);
      this.setState({
          getCouponParams:this.state.getCouponParams
      })
      this.initPageData();
  }



    render() {
        const { columns,initTableData,totalPages,totalElements,getCouponParams } = this.state;
        return (
            <div>
                <div className='sellManageCont'>
                    <Card
                        title=""
                        extra={<div>
                                  <Button onClick={()=>(this.toggleUseStyle())}></Button>
                                  <Button type="primary" style={{marginLeft:"10px"}} onClick={()=>{this.editCoupon()}}>+</Button>
                               </div>}>
                        <Row gutter={60} className="sellManagementCont">
                            <Col span={24}>
                                {/* <Input placeholder="" style={{ width: 200 }} value={this.state.coupon} onChange={(e)=>{this.couponChange(e)}}></Input>
                                <Select value={this.state.status} style={{ width: 140, margin: "0 20px 20px"}} onChange={(e)=>{this.statusChange(e)}}>
                                  <Option value="1"></Option>
                                  <Option value="2"></Option>
                                  <Option value="3"></Option>
                                  <Option value="4"></Option>
                                </Select>
                                <Button type="primary" onClick={(e)=>{this.sureBtn(e)}}></Button> */}
                            </Col>

                            <Col span={24}>
                                <Table columns={columns} 
                                       dataSource={initTableData} 
                                       pagination={false}
                                       onChange={(e,filters,sorter) => { this.paginationOnChange(e,filters,sorter) }}
                                       rowKey="couponId" />
                            </Col>

                            <Col span={24} className="pagCont">
                                <span>{totalElements}, {getCouponParams.pageNum} / {totalPages ? totalPages : "1"} </span>
                                <Pagination showSizeChanger 
                                    onShowSizeChange={this.onShowSizeChange}
                                    showQuickJumper 
                                    pageSizeOptions={["10","20","50","100"]}
                                    onChange={(e) => { this.paginationOnChange(e) }}
                                    defaultCurrent={Number(getCouponParams.pageNum)} 
                                    defaultPageSize={Number(getCouponParams.pageSize)} 
                                    total={Number(totalElements)} />
                            </Col>
                        </Row>
                    </Card>                    

                </div>
            </div>
        )
    }
}
